import React, { Component } from 'react';
import Marquee from "react-fast-marquee";
import stockImage from '../assets/designer.jpeg';

class AboutMe extends Component {
  constructor(props) {
    super(props);
    this.aboutSection = React.createRef();
    this.state = {
      scroller: {
        speed: 5
      }
    }
  }

  render() {
    return (
      <div className='aboutMeContainer'>
        <div className="marquee-container">
          <Marquee className='scroller' speed='200' direction='right'>
            MICHAL SINU MICHAL SINU MICHAL SINU MICHAL SINU MICHAL SINU MICHAL SINU MICHAL SINU MICHAL SINU MICHAL SINU MICHAL SINU MICHAL SINU
          </Marquee>
        </div>


          <div className="row">
            <div className='col-sm-5'>
              <img src={stockImage} className='stockImage' alt="Stock" />
            </div>
            <div className='col-sm-7 aboutMeContent'>
            <div className='aboutMeTypography'>
              <h1>Nco</h1>
              <h1>Malho</h1>
              <h1>O m. </h1>
            </div>
            <p>
              Jsem Michal Sinu, vvoj webovch rozhran, kter se neustle sna pekonvat oekvn. Mm clem je vytvet nejen krsn a esteticky pjemn webov strnky, ale tak ty, kter poskytuj uivatelm maximln komfort a snadnou pouitelnost. S mm pelivm okem pro detail se vnuji kadmu aspektu designu a vvoje, abych zajistil, e kad prvek na strnce m sv msto a pispv k celkovmu uivatelskmu zitku.<br />
              <br />
              Mm siln zklad v modernch technologich, jako jsou HTML, CSS a JavaScript, a neustle sleduji trendy a novinky ve svt front-end vvoje. Dky tomu jsem schopen aplikovat nejnovj postupy a techniky, abych zajistil, e m prce je vdy aktuln a konkurenceschopn.<br />
              <br />
              Mm poslnm je nejen vytvet skvl webov strnky, ale tak inspirativn pispvat k rozvoji oboru. Proto se aktivn zapojuji do komunity vvoj, sdlm sv znalosti a zkuenosti a spolupracuji na projektech, kter pinej inovace a posouvaj hranice monost webovch technologi.<br />
              <br />
              Ve sv prci se snam najt rovnovhu mezi kreativitou a funkcionalitou, abych doshl optimlnho vsledku pro kad projekt, na kterm pracuji. Jsem pesvden, e kombinace vn, znalost a peliv prce je klem k spchu ve front-end vvoji, a jsem hrd, e mohu bt soust tto dynamick a inovativn komunity.
            </p>
           </div>
          </div>
        <div className="marquee-container">
          <Marquee className='scroller scrollerInverted' speed='200'>
            O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M O M
          </Marquee>
        </div>
      </div>
    );
  }
}

export default AboutMe;
